.scroll-container {
    position: relative;
    height: 30px;
    overflow: auto;
  }
  
  .content {
    height: 30px;
    background-color: rgb(252, 255, 255);
    color: rgb(0, 0, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: 500;
  }
  
  .scroller {
    position: fixed;
    top: 0;
    right: 0;
    width: 4px; /* Adjust the width as needed */
    background-color: #333; /* Adjust the background color as needed */
    transition: height 0.2s ease-in-out;
  }
  
  
  